import React, { Component } from "react";
import { css } from 'emotion'

class Projects extends Component {
  render() {
    return (
      <div className=" projects-wrapper" className={css`
        background-color: rgba(38, 36, 36, .50);
        text-align: center;
        padding-top: 10px;
      `}>
        <h1 className={css`color: #82d8d8;`}>
        
          Projects: {this.props.filterWord ? this.props.filterWord : "All"}
        </h1>
        <div id="projects" className={css`
          display: flex;
          flex-wrap: wrap;
          color: white;
          align-content: space-between;
          justify-content: space-between;
          padding: 10px;
        `}>
          {this.props.filterWord
            ? this.props.projectData.map((project, i) => {
              console.log(this.props.filterWord);
              return (
                project.keywords.indexOf(this.props.filterWord) !== -1 && (
                  <div className="project" key={i} className={css`
                    padding: 20px;
                    color: white;
                    display: flex; 
                    flex-direction: column;
                    justify-content: space-between;
                    align-items: space-between;
                    background-color: rgb(23, 21, 21);
                    margin: 4px;
                    max-width: 350px;
                    min-width: 23%;
                    border-radius: 3px;
                  `}>
                    <div>
                      <h2 className={css` color: #82d8d8;`}>{project.title}</h2>
                      <a href={project.link} target="blank">
                        <img
                          src={project.image}
                          alt=""
                          width="100%"
                          className="projectImg"
                        />
                      </a>
                    </div>
                    <div className="cardBottom">
                      <p className="projectDesc">{project.description}</p>
                      <div className="keywords">
                        <code> {"[ " + project.keywords.join(", ") + " ]"} </code>
                      </div>
                    </div>
                  </div>
                )
              );
            })
            : this.props.projectData.map((project, i) => {
              return (
                <div className="project" key={i} className={css`
                  padding: 20px;
                  color: white;
                  display: flex; 
                  flex-direction: column;
                  justify-content: space-between;
                  align-items: space-between;
                  background-color: rgb(23, 21, 21);
                  margin: 4px;
                  max-width: 350px;
                  min-width: 23%;
                  border-radius: 3px;
                `}>
                  <div>
                    <h2 className={css` color: #82d8d8;`}>{project.title}</h2>
                    <a href={project.link} target="blank">
                      <img
                        src={project.image}
                        alt=""
                        width="100%"
                        className="projectImg"
                      />
                    </a>
                  </div>
                  <div className="cardBottom">
                    <p className="projectDesc">{project.description}</p>
                    <div className="keywords">
                      <code> {"[ " + project.keywords.join(", ") + " ]"} </code>
                    </div>
                  </div>
                </div>
              );
            })}
        </div>
      </div>
    );
  }
}

export default Projects;
